interface Attribute {
  name: string;
  value: string;
}

export function getAttributes(el: Element): object {
  const attributes = {};
  for (let i = 0; i < el.attributes.length; i++) {
    const attr = el.attributes[i];
    attributes.push({
      name: attr.name,
      value: attr.value,
    });
  }

  return attributes;
}

export function createElement(
  tagName: string,
  attributes: Attribute[],
  children: string[]
): string {
  let result = `<${tagName}`;
  for (let i = 0; i < attributes.length; i++) {
    const attribute = attributes[i];
    result += ` ${attribute.name}="${attribute.value}"`;
  }
  result += ">";
  for (let i = 0; i < children.length; i++) {
    result += children[i];
  }
  result += `</${tagName}>`;
  return result;
}
